@mixin mobile-titleText {
  font: normal 800 17px $fonts;
  text-transform: uppercase;
  text-align: center;
  margin: 20px auto;
  color: $secondary;
  letter-spacing: 2px;
}

@mixin mobile-titeH1 {
  font: normal bold 15px $fonts;
  letter-spacing: 2px;
  margin: 27px auto;
  color: $black;
  text-transform: capitalize;
  text-align: center;
}

@mixin mobile-paraFont {
  font: normal 14px/ 20px $fonts;
  letter-spacing: 1px;
  color: $secondary;
  margin: 29px auto;
  text-align: left;
}

@mixin mobile-clip {
  font: normal 800 26px /40px $fonts;
  background: $normal;
  background-clip: text;
  text-align: center;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
