@import "./variables";
@import "./responsive-mixing";
@import "./mixing";

* {
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
}

.para-font {
  font: $para-font;
  color: $secondary;
}

.title-text {
  font: $Title-Text;
  border: 2px solid black;
  text-align: center;
  text-transform: uppercase;
}

.title-h1 {
  font: $Title-h1;
  text-transform: capitalize;
}

.container {
  height: 100vh;
  position: relative;
  background: $ultimate-accent, url("../img/fordesktop.png") no-repeat center;
}

.main {
  &__cover {
    margin-top: 20px;
  }
  &__box {
    width: 385px;
    background: $primary;

    height: auto;
    padding: 14px;
    position: absolute;
    bottom: 12px;
    right: 10px;
    margin: auto;
    & > h4 {
      @include mobile-titleText;
    }
    & > h5 {
      @include mobile-titeH1;
    }
    & > p {
      @include mobile-paraFont;
    }
  }
  &__inner-box {
    display: flex;

    flex-wrap: wrap;
    justify-content: space-between;
    align-content: flex-end;
    width: 100%;
    // border: 2px solid black;
  }

  &__time {
    height: auto;
    width: 80px;
    background: $secondary;
    padding: 13px;
    & > h5 {
      @include mobile-clip;
    }
    & > p {
      text-align: center;
      text-transform: uppercase;
      font: normal 500 12px /10px $fonts;
      color: $gray;
      letter-spacing: 1px;
    }
  }
}
