$primary: rgba(230, 244, 241, 0.5);
$gray: #a5a5a5;
$black: #4c4c4c;
$secondary: #324a51;
$accent1: rgba(0, 221, 255, 0.331);
$accent2: rgba(59, 242, 208, 0.333);
$normal: linear-gradient(135deg, rgb(0, 221, 255), rgb(59, 242, 208));
$ultimate-accent: linear-gradient(135deg, $accent1, $accent2);

$fonts: Arial, "Helvetica Neue", Helvetica, sans-serif;

$para-font: normal 500 23px/ 25px $fonts;

$Title-Text: normal bold 50px/ 72px $fonts;

$Title-h1: normal bold 39px/ 40px $fonts;
